<template>
  <div>
    <el-select v-model="blogTagArray"
               @change="selectTag"
               multiple
               filterable
               allow-create
               default-first-option
               placeholder="请选择或输入标签"
               style="width: 100%;">
      <el-option v-for="item in blogTagOptions"
                 :key="item.content"
                 :label="item.content"
                 :value="item.content">
      </el-option>
    </el-select>
  </div>
</template>


<script>
export default {
  data () {
    return {
      blogTagOptions: '',
      blogTagArray: this.initData.split(';')
    }
  },
  props: ["initData"],
  mounted () {
    var a = JSON.parse(window.localStorage.getItem('BlogTag'))
    if (a) {
      this.blogTagOptions = a


    } else {

      var api_token = ""
      if (window.sessionStorage.getItem('apiToken')) {
        api_token = window.sessionStorage.getItem('apiToken')
      } else {
        this.$router.push('login')
      }
      this.$API.getBlogTag({ 'api_token': api_token }).then(
        res => {
          this.blogTagOptions = res.data.data;
        }
      ).catch(err => {
        console.log(err)
      })
    }

  },
  methods: {
    selectTag (data) {

      this.$emit('select', data)
    }
  }


}
</script>>


